<template>
  <div class="personal-center">
    <m-header :normalRightShow="false" @handleSetting="goSetting" @handleNews="goToast"></m-header>
    <div class="header">
      <div class="account" @click="goLogin" v-if="!comToken">
        <p class="avatar"><img src="../assets/images/avatar.png" alt=""></p>
        <p class="name">点击登录</p>
        <p class="arrow"></p>
      </div>
      <div class="account" v-if="comToken">
        <p class="avatar"><img src="../assets/images/avatar.png" alt=""></p>
        <p class="name" v-if="token">{{userInfo.name}}</p>
        <p class="arrow"></p>
      </div>
    </div>
    <div class="my-order">
      <div class="title-see">
        <p class="title">订单列表</p>
        <p class="see" @click="goList(2,'')">查看全部<i></i></p>
      </div>
      <div class="order-status">
        <!--<router-link tag="div" to="/myOrder" class="item">-->
        <div class="item" @click="goList(0,'0')">
          <img src="../assets/images/unpay.png" alt="">
          <p class="title">待支付</p>
        </div>
        <!--</router-link>-->
        <div class="item" @click="goList(1,'4')">
          <img src="../assets/images/gurantee.png" alt="">
          <p class="title">保障中</p>
        </div>
        <div class="item" @click="goToast">
          <img src="../assets/images/renewed.png" alt="">
          <p class="title">待续保</p>
        </div>
      </div>
    </div>
    <div class="my-info">
      <ul>
        <li>
          <div class="info-item" @click="goGuaranty(1,'3')">
            <i><img src="../assets/images/icon_group.png" alt=""></i>
             <div class="name-icon">
                <span>我的团险</span>
                <em></em>
             </div>
          </div>
        </li>
        <li class="li-claim">
          <div class="info-item" @click="goClaimList(3,'')">
            <i><img src="../assets/images/icon_claim.png" alt=""></i>
            <div class="name-icon">
            <span>我的理赔</span>
              <em></em>
            </div>
          </div>
        </li>
        <li @click="goToast">
          <!--<router-link to="/smsLogin" tag="div" class="info-item">-->
          <div class="info-item">
            <i><img src="../assets/images/icon_collect.png" alt=""></i>
            <div class="name-icon">
              <span>产品收藏</span>
              <em></em>
            </div>
            <!--</router-link>-->
          </div>
        </li>
        <li @click="goToast">
          <!--<router-link to="/smsLogin" tag="div" class="info-item">-->
          <div class="info-item">
            <i><img src="../assets/images/icon_insure.png" alt=""></i>
            <div class="name-icon">
              <span>常用被保险人</span>
              <em></em>
            </div>
          </div>
          <!--</router-link>-->
        </li>
        <li @click="goToast">
          <!--<router-link to="/" tag="div" class="info-item">-->
          <div class="info-item">
            <i><img src="../assets/images/icon_yb.png" alt=""></i>
            <div class="name-icon">
              <span>我的医保</span>
              <em></em>
            </div>
          </div>
          <!--</router-link>-->
        </li>
        <li @click="goToast">
          <!--<router-link to="/a" tag="div" class="info-item">-->
          <div class="info-item">
            <i><img src="../assets/images/icon_service.png" alt=""></i>
            <div class="name-icon">
              <span>我的健康服务</span>
              <em></em>
            </div>
          </div>
          <!--</router-link>-->
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import mHeader from '@/components/base/mHeader'
import { getLocalStorage } from '@/assets/js/localStorage'

export default {
  name: 'personalCenter',
  computed: {
    comToken () {
      return getLocalStorage('token')
    },
    ...mapGetters(['userInfo', 'token'])
  },
  methods: {
    // 订单列表
    goList (index, status) {
      this.SET_ORDER_TAB_CUR(index)
      this.SET_ORDER_TAB_CUR_STATE(status)
      this.$router.push({
        path: '/myOrder'
      })
    },
    // 团险列表
    goGuaranty (index, status) {
      this.SET_GUARANTY_TAB_CUR(index)
      this.SET_GUARANTY_TAB_CUR_STATE(status)
      this.$router.push({
        path: '/myGuaranty'
      })
    },
    // 理赔列表
    goClaimList (index, status) {
      this.SET_CLAIM_TAB_CUR(index)
      this.SET_CLAIM_TAB_CUR_STATE(status)
      this.$router.push({
        path: '/claimList'
      })
    },
    goToast () {
      this.$toast.center('模块功能开发中')
    },
    goLogin () {
      this.$router.push('/smsLogin')
    },
    goSetting () {
      this.$router.push('/setting')
    },
    ...mapMutations(['SET_ORDER_TAB_CUR', 'SET_ORDER_TAB_CUR_STATE', 'SET_GUARANTY_TAB_CUR', 'SET_GUARANTY_TAB_CUR_STATE', 'SET_CLAIM_TAB_CUR', 'SET_CLAIM_TAB_CUR_STATE'])
  },
  components: {
    mHeader
  }
}
</script>

