      height: pxToRem(296px);
      .account {
        padding: pxToRem(60px) pxToRem(20px) 0 pxToRem(30px);
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: pxToRem(96px);
        .avatar {
          width: pxToRem(146px);
          height: pxToRem(96px);
          overflow: hidden;
          img {
            width: pxToRem(96px);
            height: pxToRem(96px);
          }
        }
        .name {
          flex: 1;
          font-size: pxToRem(36px);
          color: #FFFFFF;
        }
        .arrow {
          background: url("../assets/images/icon_me.png") no-repeat;
          background-size: pxToRem(44px) pxToRem(44px);
          width: pxToRem(44px);
          height: pxToRem(44px);
        }
      }
    }
    .my-order {
