            display: flex;
            justify-content: space-between;
            align-items: center;
            i {
              width: pxToRem(101px);
              img {
                display: block;
                width: pxToRem(40px);
                height: pxToRem(41px);
                margin-left: pxToRem(30px);
              }
            }
            .name-icon{
              flex: 1;
              display: flex;
              border-bottom:$border-bottom-light-color;
              span {
                flex: 1;
                font-size: pxToRem(28px);
