                display: block;
                width:pxToRem(44px);
                height: pxToRem(44px);
                background: url("../assets/images/icon_right.png") no-repeat ;
                background-size: pxToRem(44px) pxToRem(44px);
              }
            }

          }
          &.li-claim {
            margin-bottom: pxToRem(20px);
            .info-item {
              span {
                border-bottom: none;
              }
            }
          }
          /*&:last-child{*/
          /*.info-item{*/
          /*span{*/
          /*border-bottom: none;*/
          /*}*/
          /*}*/
          /*}*/
        }
      }
    }
  }
</style>
